<template>
	<view class="watasi-signature">
		<view
			class="canvas-box"
			:style="{
				height: canvasHeight + 'px'
			}"
		>
			<canvas
				canvas-id="canvas"
				class="canvas"
			></canvas>
			<view class="texts" :style="{fontSize: fontSize + 'px'}">
				<text class='text'>张</text>
				<text class='text'>三</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "watasi-signature",
		data() {
			return {
				canvasWidth: 0,
				canvasHeight: 0,
				fontSize: 60
			}
		},
		mounted() {
			this.$nextTick(() => {
				uni.createSelectorQuery().select(".canvas").boundingClientRect(data => {
					this.canvasWidth = data.width
					this.canvasHeight = data.height
					let size = data.width > data.height ? data.height : data.width
					this.fontSize = size / 2
					this.ctx = uni.createCanvasContext('canvas')
				}).exec()
			})
		}
	}
</script>

<style lang="scss">
$border-color: rgba(202, 30, 29, 0.3);
.watasi-signature {
	width: 100%;
	.canvas-box {
		position: relative;
		width: 100%;
		background-color: #f2f2f2;
		.canvas {
			position: absolute;
			z-index: 9;
			width: 100%;
			height: 600rpx;
		}
		.texts {
			position: absolute;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			z-index: 2;
			.text {
				flex: 0 0 250rpx;
				height: 250rpx;
				line-height: 250rpx;
				text-align: center;
				font-weight: bold;
				color: rgba(3, 3, 3, 0.2);
				position: relative;
				border: 1px dashed $border-color;
				&:not(:last-child) {
					border-right-width: 0;
				}
				&::after, &::before {
					content: "";
					display: block;
					position: absolute;
				}
				&::after {
					top: 0;
					bottom: 0;
					left: 50%;
					transform: translate3d(-50%, 0, 0);
					border: 1px dashed $border-color;
				}
				&::before {
					left: 0;
					right: 0;
					top: 50%;
					transform: translate3d(0%, -50%, 0);
					border: 1px dashed $border-color;
				}
			}
		}
	}
}
</style>
